import React, { useMemo } from 'react'

import { ThemeType } from 'theme'

import { useApp } from 'redux/app/hooks'
import { TxTracker } from 'redux/midgard/types'

import { useTxManager } from 'hooks/useTxManager'

import { ReactComponent as TxIconDark } from 'assets/SVG/tx-black.svg'
import { ReactComponent as TxIconHidden } from 'assets/SVG/tx-hidden.svg'
import { ReactComponent as TxIconLight } from 'assets/SVG/tx-white.svg'

import { Label } from '../UIElements'
import * as Styled from './TxManager.style'
import { TxMonitor } from './TxMonitor'

export const TxManager = () => {
  const { themeType } = useApp()
  const {
    txTrackers,
    txCollapsed,
    setTxCollapsed,
    clearTxTrackers,
  } = useTxManager()

  const toggleTx = React.useCallback(() => {
    setTxCollapsed(!txCollapsed)
  }, [setTxCollapsed, txCollapsed])
  const hasTxHistory = useMemo(() => txTrackers.length > 0, [txTrackers])

  const renderAllTxTrackers = useMemo(
    () =>
      txTrackers.map((tracker: TxTracker) => (
        <TxMonitor key={tracker.uuid} txTracker={tracker} />
      )),
    [txTrackers],
  )

  const handleClearHistory = React.useCallback(() => {
    clearTxTrackers()
  }, [clearTxTrackers])

  return (
    <Styled.Container collapsed={txCollapsed}>
      <Styled.StyledButton onClick={toggleTx} round>
        {txCollapsed ? (
          themeType === ThemeType.DARK ? (
            <TxIconHidden />
          ) : (
            <TxIconHidden />
          )
        ) : themeType === ThemeType.DARK ? (
          <TxIconLight />
        ) : (
          <TxIconDark />
        )}
      </Styled.StyledButton>
      {!txCollapsed && (
        <>
          {!hasTxHistory && (
            <Styled.EmptyContent>
              <Label>No transactions.</Label>
            </Styled.EmptyContent>
          )}
          {hasTxHistory && (
            <Styled.Content>
              {renderAllTxTrackers}
              <Styled.ClearRow onClick={handleClearHistory}>
                <Label>Clear History</Label>
              </Styled.ClearRow>
            </Styled.Content>
          )}
        </>
      )}
    </Styled.Container>
  )
}
